import React, {Component, useState} from 'react';
import { Editor } from 'react-draft-wysiwyg';
import { EditorState } from 'draft-js';
import 'react-draft-wysiwyg/dist/react-draft-wysiwyg.css';
import styles from '../../Assets/Quick_start_style.js';
import { withStyles } from '@material-ui/core';
import FormGroup from '@material-ui/core/FormGroup';
import FormControl from '@material-ui/core/FormControl';
import {convertToRaw, ContentState } from 'draft-js';
import draftToHtml from 'draftjs-to-html';
import htmlToDraft from 'html-to-draftjs';
import Button from '@material-ui/core/Button';

const translations =window.props &&  window.props.translations

const TextEditor = ({field, classes, addCourseDetails, value})=>{
  const [goals, addGoals] = useState(value);
    return(
          <>
          {
            !goals &&  
            <Button onClick={() => addGoals("...")}>{translations.addGoals || "Add Course Goals"}</Button>
          }
          {
            goals && 
            <UncontrolledEditor
            addCourseDetails={addCourseDetails}
            field={field}
            value={value}
          />
          }
          </>
    )
}


class UncontrolledEditor extends Component {
  constructor(props) {
    super(props);
    const html = this.props.value;
    const contentBlock = htmlToDraft(html);
    if (contentBlock) {
      const contentState = ContentState.createFromBlockArray(contentBlock.contentBlocks);
      const editorState = EditorState.createWithContent(contentState);
      this.state = {
        editorState,
        showSave:false
      };
    }
  }

  onEditorStateChange: Function = (editorState) => {
    this.setState({
      editorState,
      showSave:true
    });
  };

  saveChanges =()=>{
    this.props.addCourseDetails({"name":this.props.field.name, "value":draftToHtml(convertToRaw(this.state.editorState.getCurrentContent()))})
    this.setState({
      showSave:false
    })
    console.log("has saved")
  }

  render() {
    const { editorState } = this.state;
    return (
      <div>
        <Editor
          editorState={editorState}
          wrapperClassName="demo-wrapper"
          editorClassName="demo-editor"
          onEditorStateChange={this.onEditorStateChange}
          toolbar={{
            options:['inline', 'blockType', 'fontSize', 'list', 'textAlign', 'link']
          }}
        />
        {this.state.showSave && <Button color="primary" onClick={()=> this.saveChanges()}>{translations.saveChanges || "Save Changes"}</Button> }
      </div>
    );
  }
}

export default withStyles(styles)(TextEditor)










